@import "../../assets/styles/mixin";
@import "../../assets/styles/variables";

.login-container {
	@include linearGradient(#fcee6c, #fff);

	height: 99vh;
	border-top: 1px solid #fcee6c;

	.login-box {
		width: 320px;
		margin: 0 auto;
		border-radius: 4px;
		overflow: hidden;
		box-shadow: inset 1px 1px 5px #000;
		padding: 2px;
		margin-top: 180px;

		.box-head {
			@include linearGradient(#242424, #000);

			color: #fff;
			text-align: center;
			line-height: 44px;
			border-radius: 4px 4px 0 0;
		}

		.pure-form {
			background-color: #232323;
			padding: 30px;
			border-radius: 0 0 4px 4px;

			input {
				background-color: #141414;
				border-color: #141414;
				box-shadow: none;
				top: 0;
				padding: 12px;
				color: #fff;
				margin: 0;
				border-bottom-color: #3c3c3c;

				&:last-child {
					border-bottom-color: transparent;
				}
			}

			.pure-button {
				color: #fff;
				padding: 0.8em 1em;
				background-color: #14c1b1;
			}
		}

		.oops {
			color: #fff;
			margin-top: 10px;

			> a {
				margin-left: 5px;
				color: $defaultLinkColor;

				&:hover {
					color: $defaultLinkDeepColor;
				}

				&.right {
					float: right;
				}
			}
		}
	}
}



