.head-menu {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	background-color: rgba(255, 255, 255, 0.85);
	z-index: 10;

	.pure-menu-heading {
		padding-left: 0;
	}

	.logo {
		margin-left: 20px;
		width: 30px;
		height: 30px;
		vertical-align: middle;
	}

	.pure-menu-link {
		cursor: pointer;
	}

	// .pure-menu-children {
	// 	right: 0;
	// 	text-align: center;
	// }

	.oops-menu {
		display: none;
		position: absolute;
		right: 0;
		top: 0;

		.pure-menu-item {
			padding: 0.5em;
			cursor: pointer;
			width: 28px;
			height: 22px;
			text-align: center;
			position: absolute;
			right: 0;
			transition: all .3s ease;

			&.fade-enter, &.fade-leave-active {
				opacity: 0;

				// transform: rotate(90deg);
		
			}

			&.fade-enter-active, &.fade-leave {
				opacity: 0.8;

				// transform: rotate(90deg);
		
			}
		}
	}
}

.account-menu {
	float: right;

	.pure-menu-sub-item {
		line-height: 24px;
	}

	.pure-menu-children {}

	.pure-menu-allow-hover {
		&:hover {
			.pure-menu-link, .pure-menu-children {
				background-color: #f9f9f9;
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.head-menu {
		.account-menu {
			height: 0;
			overflow: hidden;
			transition: all 0.5s;
			display: block;
			float: none;
			border-top: 1px solid #d4d4d4;

			> .pure-menu-item {
				display: block;
				text-align: center;

				&.pure-menu-allow-hover:hover {
					.pure-menu-link, .pure-menu-children {
						background-color: #fff;
					}
				}

				> .pure-menu-sub-item {
					display: none;
				}
			}

			.pure-menu-children {
				display: block;
				position: initial;
				text-align: center;
				width: 100%;

				.pure-menu-item {
					&:hover {
						.pure-menu-link {
							background-color: #f9f9f9;
						}
					}
				}
			}
		}

		.oops-menu {
			display: inline-block;
		}

		&.show {
			.account-menu {
				height: initial;
			}
		}
	}
}

@media screen and (max-width: 568px) {
	.head-menu {
		text-align: center;

		.main-menu {
			height: 0;
			overflow: hidden;
			display: block;
			float: none;
			border-top: 1px solid #d4d4d4;

			.pure-menu-item {
				display: block;
			}
		}

		.account-menu {
			border-top: none;
		}

		&.show {
			.main-menu {
				height: initial;
			}

			.account-menu {
				border-top: none;
			}
		}
	}
}



