.pure-button {
	.fa-spinner {
		animation-fill-mode: both;
		animation: rolling 2s infinite linear;
		display: inline-block;
	}
}

@keyframes rolling {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}



